import Vue from 'vue';
import MyButton from '@/components/MyButton.vue';
import HelloWorld from '@/components/HelloWorld.vue';
import Modal from '@/components/Modal.vue';
import Row from '@/components/grid/Row.vue';
import Column from '@/components/grid/Column.vue';
import Carousel from '@/components/Carousel.vue';
import DataTable from '@/components/DataTable.vue';


export default Vue.extend({
    name: 'Home',
    components: {
        MyButton,
        HelloWorld,
        Modal,
        Row,
        Column,
        Carousel,
        DataTable,
    },

    data() {
        return {
            size: '',
            color: '',
            arrayUrl: ['https://loremflickr.com/420/240?random=1', 'https://loremflickr.com/420/240?random=2',
                'https://loremflickr.com/420/240?random=3', 'https://loremflickr.com/420/240?random=4'],
            items: [{id: 30050, name: '', surname: '', age: 12},
                {id: 30051, name: '', surname: '', age: 15},
                {id: 30052, name: '', surname: '', age: 1},
                {id: 30053, name: '', surname: '', age: 5}],
            columns: [{title: '', value: '_index'},
                {title: '', value: 'name'},
                {title: '', value: 'surname', sortable: true},
                {title: '', value: 'age', type: 'number', sortable: true}],
            search: {
                fields: ['name', 'surname'],
                filters: [
                    (v: string): string => v.toLowerCase(), //     
                    // (v: string) => toKeyboardLayoutRuEn(v.toLowerCase()),
                ],
            },
        };
    },

});

